import { InputNumber, Form } from "antd";
import React from "react";

const CInputNumber = ({
  label,
  validate,
  name,
  isRequired,
  isDisabled,
  onChange = () => {},
  value,
  id,
  addonBefore,
  autoFocus,
  ...rest
}) => {
  return (
    <Form.Item
      label={
        <span>
          {label}
          {isRequired ? (
            <span style={{ marginLeft: "5px", color: "red" }}>*</span>
          ) : null}
        </span>
      }
      name={name}
      {...rest}
      validateStatus={validate ? "error" : null}
      help={validate}
    >
      <InputNumber
        addonBefore={addonBefore}
        autoFocus={autoFocus}
        value={value}
        defaultValue={value}
        id={id}
        disabled={isDisabled}
        placeholder={label}
        onChange={onChange}
        style={{ width: '100%' }}
      />
    </Form.Item>
  );
};

export default CInputNumber;
